.Main{
display: flex;
justify-content: space-around;
flex-wrap: wrap;
}
.child{
    padding: 20px;
    margin: 8px 10px;    
    background-color: #312f2e; 
    width: 35%;
    transition: .5s ease-in;
    max-width: 300px;
}
.child:hover{
   box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5); 
   background-color: #312f2ec4; 
}

.child img{
    width: 100%;
}
.load{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    width: 100%;
    background-image: url('https://images.pexels.com/photos/2205270/pexels-photo-2205270.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260');
    background-position: center;
    background-size: cover;
    transition: .5s ease-in;
    position: relative;
    background-repeat: no-repeat; 

}
.load h3{
    font-family: 'Sacramento';
    font-size: 24px;
    color: rgb(255, 251, 251);
    letter-spacing: 4px;
}

.details button{
   padding: 7px 15px;
   outline: none;
   background :#ff7961;
   border: none;
   color: rgb(248, 241, 241);
   text-align: center;
   cursor: pointer;
}
.details h4{
    font-family: 'Dancing Script';
    font-size: 18px;
    text-transform: capitalize;
    color:  #c59d5f;
    letter-spacing: 1px;
}
.details p{
    font-family: 'Cinzel';
    font-size: 14px;
    color: #bdbab8; 
}

.nav{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 30vh;
    background-image: url('https://images.pexels.com/photos/2205270/pexels-photo-2205270.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260');
    background-position: center;
    background-size: cover;
    transition: .5s ease-in;
    position: relative;
    background-repeat: no-repeat; 
}

.overlay{
    position: absolute;
    width: 100%;
    right: 0;
    left: 0;
    height: 100%;
    background-color: #a0685e33;
}

.nav h3{
    color: rgb(255, 255, 255);
    font-family: 'Great Vibes';
    font-size: 34px;
    letter-spacing: 4px;
    z-index: 200;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5); 
}
.ur{
    padding: 5px 10px;
    text-decoration: none;
    margin: 0 4px;
}
.Main2{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.search{
    width: 100%;
    padding: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px auto;
    background-color: #312f2e; 
    color: #c59d5f;
}
.search form {
    padding: 20px;
    background: rgba(255, 255, 255, 0.363);
}
.search input{
    padding: 5px 10px;
    outline: none;
    border-radius: 4px;
}
.search button{
    padding: 5px 10px;
    margin: 0 5px;
    border: none;
    cursor: pointer;
    outline: none;
   background-color: #c59d5f;
   border-radius: 4px;
}
.search h3{
    font-family: 'Great Vibes';
    letter-spacing: 2px;
    font-size: 20px;
}

.footer{
    width: 100%;
    padding: 20px 0;
    display: flex;
    position: relative;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background-color: #312f2e; 
}

.footer p{
    color: #c59d5f;
    text-transform: uppercase;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.nav2{
    display: flex;
    justify-content: space-around;
    padding: 0 auto;
    background-color: #312f2e; 
   
}
.Main3{
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: space-around;
    margin: 10px auto;
    background-color: #312f2e; 
    color: #c59d5f;
}
nav ul{
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: center;
    padding: 3px;
}
nav ul li {
    color:  #c59d5f;
    margin: 0 6px;
    text-decoration: none;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 14px;
}
.nav2 span{
    color: #adaaa8;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 100;
    letter-spacing: 10px;
}
.nav2 h3{
    color: #c59d5f;
    font-family: 'Great Vibes';
    letter-spacing: 2px;
    font-size: 20px;
}

@media screen and (max-width:1024px){
    .child{
        width: 100%;
        margin: 10px;
        max-width: 300px;
    }
}
@media screen and (max-width:768px){
    .child{
        width: 50%;
        margin: 10px auto;
    }
}
@media screen and (max-width:500px){
    .child{
        width: 100%;
        margin: 20px  auto;
    }
    .nav2 h3{
        font-size: 16px;
    }
    .nav2 span{
        letter-spacing: 1px;
    }
}